import React, { Component } from 'react';
export default class Header extends Component {
  render() {

    let resumeData = this.props.resumeData;

    return (
      <React.Fragment>
        {/*generated code*/}
        <header id="home">
          <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
            <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
            <ul id="nav" className="nav">
              <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
              <li><a className="smoothscroll" href="#skills">Skills</a></li>
              <li><a className="smoothscroll" href="#portfolio">Projects</a></li>
              <li><a className="smoothscroll" href="#about">About</a></li>
              <li><a className="smoothscroll" href="#resume">Education</a></li>
              <li><a className="smoothscroll" href="#resume">Work</a></li>
              {/*<li><a className="smoothscroll" href="#resume">Resume</a></li>*/}
              {/*<li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>*/}
              <li><a className="smoothscroll" href="#contact">Contact</a></li>
            </ul>
          </nav>

          <div className="row banner">
            <div className="banner-text">
              <h1 className="responsive-headline">Hey there, I'm {resumeData.name}</h1>
              <h3>I am a {resumeData.role}.{resumeData.roleDescription}
              </h3>
              <hr />
              <ul className="social">
                {
                  resumeData.socialLinks && resumeData.socialLinks.map(item => {
                    return (
                      <li key={item.name}>
                        <a href={item.url} target="_blank"><i className={item.className}></i></a>
                      </li>
                    )
                  }
                  )
                }
              </ul>
            </div>

            <div className="work" >

              <h5 style={{ color: 'white' }}>
                Click <a href="../Umesh_M_Resume.pdf" download>here</a> to get my resume, or click <a href="https://drive.google.com/file/d/1kozgurazc4GDNB5rp0m2tCftSyIMqlOV/view?usp=sharing" target='_blank'>here</a> to view it online .
              </h5>

            </div>
          </div>

          <p className="scrolldown">
            <a className="smoothscroll" href="#skills"><i className="icon-down-circle"></i></a>
          </p>

        </header>
      </React.Fragment>
    );
  }
}