import React, { useEffect, useState } from 'react';
import Swal from 'sweetalert2';

const ContactForm = () => {

    const [formData, setFormData] = useState({
        name: "",
        email: "",
        message: ""
    });
    const [formSubmitMessage, setFormSubmitMessage] = useState("");

    useEffect(() => {

    }, []);

    const setName = (e) => {
        setFormData({ ...formData, name: e.target.value });
    }
    const setEmail = (e) => {
        setFormData({ ...formData, email: e.target.value });
    }
    const setMessage = (e) => {
        setFormData({ ...formData, message: e.target.value });
    }

    const handleSubmit = (event) => {
        event.preventDefault();

        if (!formData.name) {
            setFormSubmitMessage("Please Enter Name !");
            return;
        }

        if (!formData.email) {
            setFormSubmitMessage("Please Enter Email !");
            return;
        }

        if (!formData.message) {
            setFormSubmitMessage("Please Enter Message !");
            return;
        }

        sendMessage();

        setFormData({
            name: '',
            email: '',
            message: '',
        })
    }

    const sendMessage = async () => {

        const contactData = { ...formData }

        let response = await fetch("https://portfolio-umesh.herokuapp.com/addContactDetails", {
            method: "POST",
            body: JSON.stringify(contactData),
            headers: {
                "Content-Type": "application/json"
            }
        })

        response = await response.json();
        if (response.stat == "200") {
            //setLoading(false);
            //setFormSubmitMessage("Message sent successfully ,I'll be sure to get back to you !");
            Swal.fire({
                title: "Message sent successfully !",
                text: "I'll be sure to get back to you !",
                icon: 'success',
                confirmButtonText: 'OK'
              })
        } else {
            setFormSubmitMessage("Oops, these was an issue in sending your message !, please try again later");
        }

    }

    return (
        <>
            <form onSubmit={handleSubmit}>
                <input
                    type="text"
                    name="name"
                    placeholder="Name"
                    onChange={setName}
                    value={formData.name || ''}
                />
                <input
                    type="email"
                    name="email"
                    placeholder="Email"
                    onChange={setEmail}
                    value={formData.email || ''}
                />
                <textarea
                    type="text"
                    name="message"
                    placeholder="Message"
                    onChange={setMessage}
                    value={formData.message || ''}
                ></textarea>
                <button type="submit">Submit</button>
            </form>
            {formSubmitMessage ? formSubmitMessage : ""}
        </>
    )
}

export default ContactForm;