import React, { Component } from 'react';
export default class Porfolio extends Component {

  constructor(props) {
    super(props);
    this.state = {
      projects: [],
      fullStackProjects: [],
      backendProjects: [],
      frontendProjects: [],
    }

    this.handleAllProjects = this.handleAllProjects.bind(this);
    this.handleFullStackProjects = this.handleFullStackProjects.bind(this);
    this.handleFrontendProjects = this.handleFrontendProjects.bind(this);
    this.handleBackendProjects = this.handleBackendProjects.bind(this);
  }

  handleAllProjects(event) {
    this.setState({ projects: this.props.resumeData.portfolio });
  }

  handleFullStackProjects(event) {
    this.fullStackProjects = this.props.resumeData.portfolio.filter(function (el) { return el.type == "fullstack" });
    this.setState({ projects: this.fullStackProjects });
  }

  handleFrontendProjects(event) {
    this.frontendProjects = this.props.resumeData.portfolio.filter(function (el) { return el.type == "frontend" });
    this.setState({ projects: this.frontendProjects });
  }

  handleBackendProjects(event) {
    this.backendProjects = this.props.resumeData.portfolio.filter(function (el) { return el.type == "backend" });
    this.setState({ projects: this.backendProjects });
  }

  componentDidMount() {
    this.setState({ projects: this.props.resumeData.portfolio });
  }

  /*resumeData.portfolio && resumeData.portfolio.map((item) => {*/

  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">

            <h1>Check Out Some of My Work</h1>

            <div className="twelve columns collapsed">
              <h1><button className={"button1"} onClick={this.handleAllProjects}>All Projects</button></h1>
            </div>

            <div className="four columns collapsed">
              <h1><button className={"button1"} onClick={this.handleFullStackProjects}>Full Stack</button></h1>
            </div>
            <div className="four columns collapsed">
              <h1><button className={"button1"} onClick={this.handleFrontendProjects}>Front End</button></h1>
            </div>
            <div className="four columns collapsed">
              <h1><button className={"button1"} onClick={this.handleBackendProjects}>Back End</button></h1>
            </div>

            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              {

                this.state.projects && this.state.projects.map((item) => {
                  return (
                    <div className="columns portfolio-item">
                      <div className="item-wrap">

                        <img src={`${item.imgurl}`} className="img-fluid" alt="description of" className="item-img" />
                        {/*<img src="../images/portfolio/console.jpg" alt="description of" id="image" name="something" className="item-img"/>*/}

                        <div className="overlay">
                          <div className="portfolio-item-meta">
                            <h5>{item.name}</h5>
                            <p>{item.description}</p>
                            <br></br>
                            <p>{item.technologiesUsed}</p>
                          </div>
                        </div>

                      </div>
                      <a href={item.srcurl} target='_blank'>
                        <span>View Source - </span>
                      </a>
                      <a href={item.deployurl} target='_blank'>
                        <span>View Demo</span>
                      </a>
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      </section>
    );
  }
}