import React, { Component } from 'react';

export default class Skills extends Component {

    render() {

        //let resumeData = this.props.resumeData;

        let newSkills = {
            "skills": [
                {
                    "skillname": "Javascript",
                    "progress": "75%"
                },
                
                {
                    "skillname": "Node.js / Express",
                    "progress": "80%"
                },
                {
                    "skillname": "React",
                    "progress": "60%"
                },
                {
                    "skillname": "Java / SpringBoot",
                    "progress": "70%"
                },
                {
                    "skillname": "MongoDB / NoSql",
                    "progress": "65%"
                },
                {
                    "skillname": "AWS / GCS",
                    "progress": "50%"
                },
                {
                    "skillname": "HTML / CSS",
                    "progress": "70%"
                },
                /*
                {
                  "skillname": "Containerization \xa0\xa0\xa0\-\xa0\xa0\xa0 Docker / VirtualBox"
                },
                */
                {
                    "skillname": "DevOps",
                    "progress": "55%"
                },
                {
                    "skillname": "Testing",
                    "progress": "60%"
                },
                
                {
                    "skillname": "Netlify / Heroku",
                    "progress": "80%"
                },
            ],
        }

        let resumeData = newSkills;

        const divStyle = {
            margin: '40px',
            color: '#fff'
        };
        const pStyle = {
            fontSize: '25px',
            textAlign: 'center'
        };

        let progressLevel = {
            width: ""
        }

        return (

            <section id="skills" className="skills" >



                <div className="row work">

                    <div className="twelve columns collapsed">
                        <h1><span>Skills</span></h1>
                    </div>
                    {/*
                    <div className="three columns header-col">
                        <h1><span>Skills</span></h1>
                    </div>
                    */}
                    <div className="twelve columns main-col">


                        <div className="bars">

                            <ul className="skills">
                                {
                                    resumeData.skills && resumeData.skills.map((item) => {
                                        return (
                                            <div className="six columns">
                                                <li class="tooltip">
                                                    {progressLevel.width = item.progress}
                                                    {console.log("progressLevel.width "+progressLevel.width)}
                                                    <span className={`bar-expand`} style={{ width: item.progress}} >
                                                    <span class="tooltiptext">{item.progress}</span>
                                                    </span><em>{item.skillname}</em>
                                                </li>
                                            </div>
                                        )
                                    })
                                }
                            </ul>

                        </div>

                    </div>
                </div>

            </section>
        );
    }
}